*{
    font-family: Helvetica, Arial, sans-serif;
}

body{
    padding: 60px;
}

input {
    font-size: 1.3rem;
    width: 100%;
    color: grey;
}

.card-grid {
    display: flex;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p{
    padding: 5px;
    text-align: center;
    width: 100%;
    font-size: smaller;
}

.card img{
    max-height: 170px;
}